import { useEffect, useState } from "react";
import "./QualitySelectStyle.js";
import {
  Info,
  QSBackground,
  QSButton,
  QSButtonsContainer,
  QSContainer,
  QSName,
  QSSubHeading,
  QSTitle,
  QSTop,
  TriangleTop,
  TriangleBottom,
  TriangleLeft,
  TriangleRight,
} from "./QualitySelectStyle.js";

export default function QualitySelect({ setQuality, setShowQualityLayer }) {
  const [opacity, setOpacity] = useState(0);
  const [transition, setTransition] = useState("5s");
  const [top, setTop] = useState("60%");
  const [open, setOpen] = useState(false);
  const [showBackground, setShowBackground] = useState(true);
  const [cursor, setCursor] = useState(null);

  useEffect(() => {
    setOpacity(1);
    setTop(0);
  }, []);

  const disableQualityLayer = () => {
    setTimeout(() => {
      setShowQualityLayer(false);
    }, 800);
  };
  const onClick = (value) => {
    setCursor("wait");
    setQuality(value);
    setTimeout(() => {
      setOpacity(0);
      setTransition("800ms");
      setTop("100%");
      setShowBackground(false);
      setOpen(true);
      disableQualityLayer();
    }, 1000);
  };
  return (
    <QSBackground showBackground={showBackground}>
      <TriangleTop open={open} cursor={cursor} />
      <TriangleBottom open={open} cursor={cursor} />
      <TriangleLeft open={open} cursor={cursor} />
      <TriangleRight open={open} cursor={cursor} />
      <QSTop opacity={opacity} transition={transition}>
        <QSName>DAVID BEALE</QSName>
        <QSSubHeading>PERSONAL PROJECTS</QSSubHeading>
      </QSTop>
      <Info>*Chrome recommended*</Info>
      <QSContainer top={top}>
        <QSTitle>Select Quality</QSTitle>
        <QSButtonsContainer>
          <QSButton cursor={cursor} onClick={() => onClick(1)}>
            LOW
          </QSButton>
          <QSButton cursor={cursor} onClick={() => onClick(0)}>
            HIGH
          </QSButton>
        </QSButtonsContainer>
      </QSContainer>
    </QSBackground>
  );
}
